// 
export const UserRole = {
  SYSTEM_ADMIN: 'system_admin',    // 
  HQ_ADMIN: 'hq_admin',            // 
  AREA_MANAGER: 'area_manager',    // 
  STORE_MANAGER: 'store_manager',  // 
  FLOOR_MANAGER: 'floor_manager',  // 
  STAFF: 'staff',                 // 
  TESTER: 'tester'                // 
} as const;

export type UserRole = typeof UserRole[keyof typeof UserRole];

// 
export interface Permission {
  role: UserRole;
  permissions: string[];
}

// 
export interface Store {
  id: string;
  name: string;
  code: string; // 
  areaId: string; // 
  areaName?: string; // 
  managerId?: string; // ID
  managerName?: string; // 
  companyId?: string; // ID
  companyName?: string; // 
  address?: string; // 
  phone?: string; // 
  email?: string; // 
  status?: 'active' | 'inactive'; // 
  employeeCount?: number; // 
  isActive: boolean;
  createdAt: string;
  updatedAt: string;
}

// 
export interface Area {
  id: string;
  name: string;
  managerId?: string; // ID
  stores: Store[];
  isActive: boolean;
  createdAt: string;
  updatedAt: string;
}

// 
export interface User {
  id: string;
  lineUserId: string; // LINE ID
  name: string;
  email: string;
  phone: string;
  role: UserRole;
  storeId?: string; // 
  areaId?: string; //  ()
  isActive: boolean;
  createdAt: string;
  updatedAt: string;
}

// 
export interface ActivityTag {
  id: string;
  date: string;
  tagName: string; // 
  isLeaveRestricted: boolean; // 
  minStaffRequired: number; // 
  createdBy: string;
  createdAt: string;
  updatedAt: string;
}

// 
export interface Shift {
  id: string;
  userId: string;
  storeId: string;
  date: string;
  shiftType: 'morning' | 'evening' | 'full';
  startTime: string;
  endTime: string;
  breakTime: number; // ()
  actualHours: number; // 
  isSupportShift: boolean; // 
  originalStoreId?: string; //  ()
  targetStoreId?: string; //  ()
  status: 'scheduled' | 'completed' | 'cancelled';
  createdAt: string;
  updatedAt: string;
}

// 
export interface LeaveRequest {
  id: string;
  userId: string;
  storeId: string;
  leaveType: 'annual' | 'sick' | 'personal' | 'maternity' | 'other';
  startDate: string;
  endDate: string;
  reason: string;
  status: 'pending' | 'approved' | 'rejected';
  approverId?: string; // ID
  approvalLevel: number; // 
  maxApprovalLevel: number; // 
  createdAt: string;
  updatedAt: string;
}

// 
export interface WorkHourStats {
  userId: string;
  userName: string;
  storeId: string;
  period: string; // 2026-02
  regularHours: number; // 
  supportHours: number; // 
  totalHours: number; // 
  supportDetails: {
    targetStoreId: string;
    targetStoreName: string;
    hours: number;
  }[];
}

// 
export const hasPermission = (userRole: UserRole, requiredRole: UserRole): boolean => {
  const roleHierarchy = {
    [UserRole.SYSTEM_ADMIN]: 7,
    [UserRole.TESTER]: 6,
    [UserRole.HQ_ADMIN]: 5,
    [UserRole.AREA_MANAGER]: 4,
    [UserRole.FLOOR_MANAGER]: 3,
    [UserRole.STORE_MANAGER]: 2,
    [UserRole.STAFF]: 1
  };
  
  return roleHierarchy[userRole] >= roleHierarchy[requiredRole];
};

