import React, { useState } from 'react';
import { useCrossStoreSupport } from '../hooks/useCrossStoreSupport';
import { usePermission } from '../context/PermissionContext';
import ProtectedRoute from './ProtectedRoute';
import type { Shift } from '../types/permissions';

const CrossStoreSupportPage: React.FC = () => {
  const { 
    supportShifts, 
    loading, 
    error, 
    createSupportShift, 
    updateSupportShift, 
    cancelSupportShift,
    getSupportShiftsForStore,
    validateSupportRequest
  } = useCrossStoreSupport();
  
  const { hasPermission, currentUser, userStore } = usePermission();
  const [showCreateForm, setShowCreateForm] = useState(false);
  const [formData, setFormData] = useState({
    userId: '',
    date: '',
    shiftType: 'full' as 'morning' | 'evening' | 'full',
    startTime: '09:00',
    endTime: '18:00',
    breakTime: 1.5,
    targetStoreId: ''
  });

  // Mock 
  const userOptions = [
    { id: '4', name: '', storeId: 'store-1' },
    { id: '3', name: '', storeId: 'store-1' }
  ];

  // Mock 
  const storeOptions = [
    { id: 'store-1', name: '' },
    { id: 'store-2', name: '' }
  ];

  // 
  const shiftTypeOptions = [
    { value: 'morning', label: ' (09:00-13:00)' },
    { value: 'evening', label: ' (14:00-18:00)' },
    { value: 'full', label: ' (09:00-18:00)' }
  ];

  // 
  const handleShiftTypeChange = (shiftType: string) => {
    const timeSettings = {
      morning: { startTime: '09:00', endTime: '13:00', breakTime: 0.5 },
      evening: { startTime: '14:00', endTime: '18:00', breakTime: 0.5 },
      full: { startTime: '09:00', endTime: '18:00', breakTime: 1.5 }
    };

    setFormData(prev => ({
      ...prev,
      shiftType: shiftType as any,
      ...timeSettings[shiftType as keyof typeof timeSettings]
    }));
  };

  // 
  const calculateActualHours = () => {
    const start = new Date(`2000-01-01T${formData.startTime}`);
    const end = new Date(`2000-01-01T${formData.endTime}`);
    const totalHours = (end.getTime() - start.getTime()) / (1000 * 60 * 60);
    return Math.max(0, totalHours - formData.breakTime);
  };

  // 
  const handleCreateSupportShift = async () => {
    try {
      // 
      const validation = validateSupportRequest(
        formData.userId,
        formData.date,
        formData.targetStoreId
      );

      if (!validation.isValid) {
        alert('' + validation.error);
        return;
      }

      const selectedUser = userOptions.find(u => u.id === formData.userId);
      const shiftData: Omit<Shift, 'id' | 'createdAt' | 'updatedAt'> = {
        userId: formData.userId,
        storeId: formData.targetStoreId,
        date: formData.date,
        shiftType: formData.shiftType,
        startTime: formData.startTime,
        endTime: formData.endTime,
        breakTime: formData.breakTime,
        actualHours: calculateActualHours(),
        isSupportShift: true,
        originalStoreId: selectedUser?.storeId,
        targetStoreId: formData.targetStoreId,
        status: 'scheduled'
      };

      await createSupportShift(shiftData);
      
      // 
      setFormData({
        userId: '',
        date: '',
        shiftType: 'full',
        startTime: '09:00',
        endTime: '18:00',
        breakTime: 1.5,
        targetStoreId: ''
      });
      setShowCreateForm(false);
      
      alert('');
    } catch (err) {
      console.error(':', err);
      alert('' + (err instanceof Error ? err.message : ''));
    }
  };

  // 
  const handleCancelSupportShift = async (shiftId: string) => {
    if (!confirm('')) {
      return;
    }

    try {
      await cancelSupportShift(shiftId);
      alert('');
    } catch (err) {
      console.error(':', err);
      alert('' + (err instanceof Error ? err.message : ''));
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="w-12 h-12 border-4 border-blue-600 border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-gray-600">...</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="w-16 h-16 bg-red-100 rounded-full flex items-center justify-center mx-auto mb-4">
            <svg className="w-8 h-8 text-red-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4m0 4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
            </svg>
          </div>
          <h2 className="text-xl font-semibold text-gray-900 mb-2"></h2>
          <p className="text-gray-600">{error}</p>
        </div>
      </div>
    );
  }

  return (
    <ProtectedRoute requiredPermission="manage_store_schedule">
      <div className="min-h-screen bg-gray-50 py-8">
        <div className="max-w-7xl mx-auto px-4">
          {/*  */}
          <div className="mb-8 flex justify-between items-center">
            <div>
              <h1 className="text-3xl font-bold text-gray-900 mb-2"></h1>
              <p className="text-gray-600"></p>
            </div>
            <button
              onClick={() => setShowCreateForm(true)}
              className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700 transition-colors"
            >
               
            </button>
          </div>

          {/*  */}
          {showCreateForm && (
            <div className="bg-white rounded-lg shadow-md p-6 mb-6">
              <h2 className="text-lg font-semibold text-gray-900 mb-4"></h2>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                     *
                  </label>
                  <select
                    value={formData.userId}
                    onChange={(e) => setFormData(prev => ({ ...prev, userId: e.target.value }))}
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                    required
                  >
                    <option value=""></option>
                    {userOptions.map(user => (
                      <option key={user.id} value={user.id}>
                        {user.name} (: {user.storeId === 'store-1' ? '' : ''})
                      </option>
                    ))}
                  </select>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                     *
                  </label>
                  <input
                    type="date"
                    value={formData.date}
                    onChange={(e) => setFormData(prev => ({ ...prev, date: e.target.value }))}
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                    required
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                     *
                  </label>
                  <select
                    value={formData.shiftType}
                    onChange={(e) => handleShiftTypeChange(e.target.value)}
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                    required
                  >
                    {shiftTypeOptions.map(option => (
                      <option key={option.value} value={option.value}>
                        {option.label}
                      </option>
                    ))}
                  </select>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                     *
                  </label>
                  <select
                    value={formData.targetStoreId}
                    onChange={(e) => setFormData(prev => ({ ...prev, targetStoreId: e.target.value }))}
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                    required
                  >
                    <option value=""></option>
                    {storeOptions.map(store => (
                      <option key={store.id} value={store.id}>
                        {store.name}
                      </option>
                    ))}
                  </select>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    
                  </label>
                  <input
                    type="time"
                    value={formData.startTime}
                    onChange={(e) => setFormData(prev => ({ ...prev, startTime: e.target.value }))}
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    
                  </label>
                  <input
                    type="time"
                    value={formData.endTime}
                    onChange={(e) => setFormData(prev => ({ ...prev, endTime: e.target.value }))}
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                     ()
                  </label>
                  <input
                    type="number"
                    step="0.5"
                    min="0"
                    max="4"
                    value={formData.breakTime}
                    onChange={(e) => setFormData(prev => ({ ...prev, breakTime: parseFloat(e.target.value) || 0 }))}
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    
                  </label>
                  <div className="px-3 py-2 bg-gray-100 border border-gray-300 rounded-lg">
                    {calculateActualHours().toFixed(1)} 
                  </div>
                </div>
              </div>

              <div className="flex justify-end space-x-3 mt-6">
                <button
                  onClick={() => setShowCreateForm(false)}
                  className="px-4 py-2 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50 transition-colors"
                >
                  
                </button>
                <button
                  onClick={handleCreateSupportShift}
                  className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
                >
                  
                </button>
              </div>
            </div>
          )}

          {/*  */}
          <div className="bg-white rounded-lg shadow-md p-6">
            <h2 className="text-lg font-semibold text-gray-900 mb-4"></h2>
            <div className="overflow-x-auto">
              <table className="min-w-full divide-y divide-gray-200">
                <thead className="bg-gray-50">
                  <tr>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      
                    </th>
                  </tr>
                </thead>
                <tbody className="bg-white divide-y divide-gray-200">
                  {supportShifts.map((shift) => {
                    const user = userOptions.find(u => u.id === shift.userId);
                    const originalStore = storeOptions.find(s => s.id === shift.originalStoreId);
                    const targetStore = storeOptions.find(s => s.id === shift.targetStoreId);
                    
                    return (
                      <tr key={shift.id}>
                        <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                          {user?.name || ''}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                          {shift.date}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                          {shift.shiftType === 'morning' ? '' : 
                           shift.shiftType === 'evening' ? '' : ''}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                          {originalStore?.name || ''}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                          {targetStore?.name || ''}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                          {shift.actualHours.toFixed(1)}h
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap">
                          <span className={`inline-flex items-center px-2 py-1 text-xs font-medium rounded-full ${
                            shift.status === 'scheduled' 
                              ? 'bg-blue-100 text-blue-800'
                              : shift.status === 'completed'
                              ? 'bg-green-100 text-green-800'
                              : 'bg-red-100 text-red-800'
                          }`}>
                            {shift.status === 'scheduled' ? '' : 
                             shift.status === 'completed' ? '' : ''}
                          </span>
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                          {shift.status === 'scheduled' && (
                            <button
                              onClick={() => handleCancelSupportShift(shift.id)}
                              className="text-red-600 hover:text-red-900"
                            >
                              
                            </button>
                          )}
                        </td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>
            </div>
          </div>

          {/*  */}
          <div className="mt-6 bg-blue-50 border border-blue-200 rounded-lg p-4">
            <h3 className="text-sm font-medium text-blue-900 mb-2"></h3>
            <div className="text-xs text-blue-700 space-y-1">
              <div><strong>:</strong> {currentUser?.name}</div>
              <div><strong>:</strong> {userStore?.name || ''}</div>
              <div><strong>:</strong> {supportShifts.length}</div>
              <div><strong>:</strong> {hasPermission('manage_store_schedule') ? '' : ''}</div>
            </div>
          </div>
        </div>
      </div>
    </ProtectedRoute>
  );
};

export default CrossStoreSupportPage;
